
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import Radio from 'vux/src/components/radio/index.vue'
import dateFormat from 'vux/src/tools/date/format.js'
import XButton from 'vux/src/components/x-button/index.vue'
import PopupRadio from 'vux/src/components/popup-radio/index.vue'
import XNumber from 'vux/src/components/x-number/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import XImg from 'vux/src/components/x-img/index.vue'

import {getAjaxData, postAjaxData} from "api/api";

let saveOk = false

export default {
components: {
XHeader,
Group,
Cell,
XInput,
Datetime,
Radio,
dateFormat,
XButton,
PopupRadio,
XNumber,
Actionsheet,
XImg
},
beforeRouteLeave(to, fromb, next) {
if ((this.formData.OrderID || this.formData.CustomerID) && this.$route.query.cId||this.$route.query.cid=='0') {
if (!saveOk) {
this.$vux.confirm.show({
title: "继续退出？",
content: "您已编辑过，退出将丢失这些内容！",
onCancel() {
next(false)
},
onConfirm() {
next()
}
});
} else {
next()
}
} else {
next()
}


},
activated(){
saveOk = false
},
mounted() {
saveOk = false
if (this.$route.query.id) {
this.pageView = "change";
this.id = this.$route.query.id;
this.getData();
}
if (this.$route.query.cId) {
this.cid = this.$route.query.cId;
this.formData.CustomerID = this.$route.query.cId;
this.formData.CustomerName = this.$route.query.cName;
this.formData.OrderNumber = this.$route.query.orderNumber;
this.formData.OrderID = this.$route.query.orderID;
this.$nextTick(() => {
this.formData.PayType = parseInt(this.$route.query.payType);
})
if (this.formData.CustomerID != 0) {
this.isSelectCust = false;
}
}
this.$bus.on("orderSelected", val => {
this.formData.OrderID = val.ID;
this.formData.OrderNumber = val.OrderNumber;
this.formData.TotalPrice = val.TotalPrice;
this.formData.PayType = val.PayType;
});


this.$bus.on("selectedClient", val => {

this.formData.CustomerID = val.ID;
this.formData.CustomerName = val.CustomerName;


this.formData.OrderID = "";
this.formData.OrderNumber = "";
this.formData.TotalPrice = 0;
this.formData.PayType = "";
});
},
data() {
return {
isSave: false,
id: "",
cid: "",
payOptions: [
{
key: 1,
value: "未付"
},
{
key: 2,
value: "支票"
},
{
key: 3,
value: "网上银行"
},
{
key: 4,
value: "电付"
},
{
key: 5,
value: "邮局汇款"
},
{
key: 6,
value: "支付宝"
},
{
key: 7,
value: "其他"
}
],
formData: {
ID: 0,
OrderID: "",
OrderNumber: "",
TotalPrice: 0,
CustomerID: 0,
CustomerName: "请选择客户",
ReturnDate: dateFormat(new Date(), "YYYY-MM-DD HH:mm:ss"),
ReturnPrice: "",
PayType: 1,
Description: "",
InvoiceMoney: "",
InvoiceDate: "",
LinkID: "",
LinkName: "",
ImgAttach:[],
},
name: "",
pageView: "add", //客户添加修改判断项
showMore: true, //是否显示更多
isSelectCust: true,
cname: "",
saveOk:false,
showAction:false,
actionMenu:{
shot: '拍照',
album: '从相册选择'
},
pics:[],
};
},
computed: {
getPayTypeName() {
let pay = this.payOptions.find(item => {
return item.key == this.formData.PayType;
});
return pay ? pay.value : "";
},
maxPic() {
return 5 - this.formData.ImgAttach.length;
}
},
methods: {
clearTime(idx) {
if (idx == 1) {
this.formData.ReturnDate = "";
} else {
this.formData.InvoiceDate = "";
}
},
inputReturnNumber(value) {
if (value === '') {
return false
}
let _value = parseFloat(value);
if(!_value){
_value = 0;
}
if (_value < 0) {
_value = 0;
}
if (_value > 10000000000) {
_value = 9999999999
}
_value = Math.floor(_value * 100) / 100
this.formData.ReturnPrice = _value;
},
inputInvoiceNumber(value) {
if (value === '') {
return false
}
let _value = parseFloat(value);
if(!_value){
_value = 0;
}
if (_value < 0) {
_value = 0;
}
if (_value > 10000000000) {
_value = 9999999999
}
_value = Math.floor(_value * 100) / 100
this.formData.InvoiceMoney = _value;
},
routerBack() {
this.$router.back();
},

getData() {
if (this.id) {
axios.get(`/Cost/GetCrmReturnedMoneyByID/${this.id}`).then(res => {
let _data = res.data
if (_data.State == 1) {
Object.assign(this.formData, _data.Data)
if(this.formData.ImgAttach){
this.formData.ImgAttach.forEach(item=>{
item.PicPath = `${api50}/api/v1/File/DownLoadPIc?filePath=${
item.FilePath
}`;
})
}
}
});
}
},

gotoCustomerSelect() {
if (!this.isSelectCust) return false;
let _path = "/addReceive/selectCust"
if (this.pageView == "change") {
_path = "/changeReceive/selectCust"
}

this.$router.push({
path: _path
});
},

gotoOrderSelect() {
if (!this.formData.CustomerID || this.formData.CustomerID == "0") {
this.$vux.toast.show({
text: "请先选择客户",
position: "middle",
type: "warn"
});
return;
}
let _path = "/addReceive/selectOrder";
if (this.pageView == 'change') {
_path = "/changeReceive/selectOrder"
} else {
if (this.cid) {
_path = "/client/clientDetails/addReceive/selectOrder"
}
}

this.$router.push({
path: _path,
query: {
cid: this.formData.CustomerID,
oid: this.formData.OrderID
}
});
},

startSelectPic(){
this.showAction=true
},
chooseSelectType(tid){
if (this.maxPic <= 0) {
this.$vux.toast.show({
text:"最多可选5张图片",
position: "middle",
type: "cancel"
})
return;
}
if(tid == 'shot'){
this.shotAndChoose()
}else if(tid=='album'){
this.addPic()
}else{

}
},
shotAndChoose(){
if (this.maxPic <= 0) {
this.$vux.toast.show({
text:"最多可选5张图片",
position: "middle",
type: "cancel"
})
return;
}
const camera = plus.camera.getCamera();
camera.captureImage(path => {
if(path){
this.selectPic([path])
}
});
},
addPic() {
let map = document.querySelector(".addReceived")
map.scrollTop = 360;

let _this = this;
if (this.maxPic <= 0) {
this.$vux.toast.show({
text:"最多可选5张图片",
position: "middle",
type: "cancel"
})
return;
}
plus.gallery.pick(
function(pics) {
_this.selectPic(pics.files);
},
function(e) {
console.log("取消选择图片");
},
{
filter: "image",
multiple: true,
maximum: _this.maxPic,
system: false
}
);
},
selectPic(pics) {
this.uploadPic(pics);
},
uploadPic(pics) {
let _this = this;
let _server = this.BillFileUrl(1);
let wt = plus.nativeUI.showWaiting();
wt.close();
let task = plus.uploader.createUpload(
_server,
{ method: "POST" },
function(t, status) {

if (status == 200) {
_this.$vux.loading.hide()
wt.close();
let res = JSON.parse(t.responseText);

if (res.State == 1) {
res.Data.forEach(item => {
item.PicPath = `${api50}/api/v1/File/DownLoadPIc?filePath=${
item.FilePath
}`;
item.ID=0;
item.FileSize=0;
_this.pics.push({src:item.PicPath})
});
} else {
_this.$vux.toast.show({
text: res.ErrorMessage,
position: "middle",
type: "cancel"
});
return false;
}
_this.formData.ImgAttach.push(...res.Data);
} else {
alert("上传失败！");
}
}
);
let _promiseList = []
for (let i = 0; i < pics.length; i++) {
let pic = pics[i]
_promiseList.push(this.zipPic(pic, i))
}

let index = 0;
task.addEventListener(
"statechanged",
(upload, status) => {
if (upload.totalSize > 1024 * 1024 * 10) {
this.$vux.toast.show({
type: "warn",
text: "图片大小不超过10M"
});
task.abort();
return false;
} else {
index++;
}
},
false
);

Promise.all(_promiseList).then((images)=>{
for(let i=0;i<images.length;i++){
task.addFile(images[i].path, { key: images[i].name});
}
}).then(()=>{
task.start();
})

},
zipPic(pic,idx){
return new Promise((resolve, reject)=>{
let reg = new RegExp('(.{' + pic.lastIndexOf('.') + '})');
var zipFile = pic.replace(reg, '$1_zip');
var zipExt = zipFile.substr(zipFile.lastIndexOf('.') + 1, zipFile.length - zipFile.lastIndexOf('.'));
var zipPath = zipFile.substr(0, zipFile.lastIndexOf('/') + 1) + idx + "." + zipExt;
let fileName = zipPath.substr(zipPath.lastIndexOf('/') + 1, zipPath.length - zipPath.lastIndexOf('/'));
plus.zip.compressImage({
src: pic,
dst: zipPath,
overwrite: true,
width: "50%",
quality: 60
}, function(e) {
resolve({path:e.target, name:fileName})
}, function(e) {
reject(e)
});
})
},
removePic(idx) {
this.formData.ImgAttach.splice(idx, 1);
this.pics.splice(idx,1)
},

addReceiveSubmit() {
console.log(this.formData)
if (!this.formData.CustomerID) {
this.$vux.toast.show({
text: "请选择客户",
position: "middle",
type: "warn"
});
return;
}
if (!this.formData.OrderNumber) {
this.$vux.toast.show({
text: "请选择订单",
position: "middle",
type: "warn"
});
return;
}
if (!this.formData.PayType) {
this.$vux.toast.show({
text: "请选择付款方式",
position: "middle",
type: "warn"
});
return;
}
if (!this.formData.ReturnDate) {
this.$vux.toast.show({
text: "请选择回款时间",
position: "middle",
type: "warn"
});
return;
}
if (!this.formData.ReturnPrice) {
this.$vux.toast.show({
text: "请输入回款金额",
position: "middle",
type: "warn"
});
return;
}

if (parseFloat(this.formData.ReturnPrice) <= 0) {
this.$vux.toast.show({
text: "回款金额应大于0",
position: "middle",
type: "warn"
});
return;
}

let postObj = this.formData;
postObj.ReturnPrice = postObj.ReturnPrice.toFixed(2);
this.$vux.loading.show({
text: "提交中..."
});
let url = "/Cost/SaveCrmReturnedMoney";
this.postAxios(url, postObj)
.then(res => {

this.$vux.loading.hide();
this.$vux.toast.show({
text:
this.pageView === "add" ? "新建回款成功!" : "修改回款信息成功",
position: "middle",
type: "success"
});
this.$bus.emit("onSave", true);
this.$bus.emit("refreshReceiveList");
setTimeout(() => {
this.$vux.toast.hide();
this.$vux.loading.hide();
if(this.pageView == 'add'){
saveOk = true;
}
this.$router.back();

}, 500);
})
.catch(err => {
this.$vux.toast.hide();
this.$vux.loading.hide();
this.$vux.toast.show({
text: err.ErrorMessage,
position: "middle",
type: "cancel"
});
});
}
},
};



